html,
body {
  height: 100%;
  color: #272727;
}

body {
  margin: 0;
  background-color: #fff;
  font: bold 15px/1.2 "Open Sans", sans-serif;
	color: #000;
	background: url(/images/img1.jpg) no-repeat 50% 50%;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

img {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.page {
  text-align: center;
  min-height: 100%;
  position: relative;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 2rem 0 5rem;
}

.header{
  margin-bottom: 3rem;
}

.currency-list__item {
  margin-bottom: 2rem;
  position: relative;
  z-index: 10;
  text-align: center;
}

.currency-list__heading {
  font-size: 3rem;
}

.page img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
}

.footer {
  position: absolute;
  bottom: 1px;
  right: 0;
  font-size: 1rem;
}

@media only screen and (max-width: 767px) and (orientation: landscape) {
  body {
    font-size: 12px;
  }
  .currency-list {
    position: static;
    transform: none;
  }
}

@media only screen and (max-width: 320px) {
  body {
    font-size: 12px;
  }
  .currency-list {
    position: static;
    transform: none;
  }
}

@media only screen and (max-height: 667px) {
  body {
    font-size: 12px;
  }
}
